:root {
  --toggle-bg-start: hsl(230, 22%, 74%);
  --toggle-bg-end: hsl(230, 22%, 74%);
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youTube: hsl(348, 97%, 39%);
  --main-bg: hsl(0, 0%, 100%);
  --main-bg-top: hsl(225, 100%, 98%);
  --card-bg: hsl(227, 47%, 96%);
  --text-color: hsl(228, 12%, 44%);
  --figure-values: hsl(230, 17%, 14%);
}

[data-theme=dark] {
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youTube: hsl(348, 97%, 39%);
  --main-bg: hsl(230, 17%, 14%);
  --main-bg-top: hsl(232, 19%, 15%);
  --card-bg: hsl(228, 28%, 20%);
  --text-color: hsl(228, 34%, 66%);
  --figure-values: hsl(0, 0%, 100%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

button {
  cursor: pointer;
}

ul,
ol {
  list-style-type: none;
}

body {
  font-family: var(--font-inter);
  background-color: var(--main-bg);
  color: var(--text-color);
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 2rem;
  line-height: 1.15;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

@media (min-width: 71.875em) {
  h2 {
    font-size: 1.5rem;
  }
}

a {
  text-decoration: none;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  font-weight: 700;
}
.header__title__heading {
  color: var(--figure-values);
}
.header__title__fw-count {
  font-size: 0.875rem;
}